import { Component, HostListener } from '@angular/core';
import { FormControl } from '@angular/forms';
import { getCodes } from './getCode';
import * as moment from 'moment';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  SECTOR_MOVEMENT_IDs = {
    CHARACTER: 1,
    DOLG: 2,
    LOGIC: 3,
    MEMORY: 4,
    LUCK: 5,
    WORK: 6,
    TRANSFORMATION: 7,
    DEGRADATION: 8
  }
  page: 'calculator' | 'catalog' | 'transformation' | 'movement' = 'calculator'
  activeTab: 'datepicker' | 'matrix' = 'datepicker'
  errMsg: string
  status: string
  dateField = new FormControl()
  matrixField = new FormControl()
  character = 0
  energy = 0
  interest = 0
  health = 0
  logic = 0
  work = 0
  luck = 0
  dolg = 0
  memory = 0
  temp = 0
  goal = 0
  byt = 0
  fam = 0
  habits = 0
  destinyNumb = null

  movementMatrix = {
    character: 0,
    energy: 0,
    interest: 0,
    health: 0,
    logic: 0,
    work: 0,
    luck: 0,
    dolg: 0,
    memory: 0,
    temp: 0,
    goal: 0,
    byt: 0,
    fam: 0,
    habits: 0
  }

  private selectedMovementItems = []

  get matrixString() {
    return this.destinyNumb
      ? `${this.returnString(this.character, '1')  || '-'}/${this.returnString(this.health, '4') || '-'}/${this.returnString(this.luck, '7') || '-'}/${this.returnString(this.energy, '2') || '-'}/${this.returnString(this.logic, '5') || '-'}/${this.returnString(this.dolg, '8') || '-'}/${this.returnString(this.interest, '3') || '-'}/${this.returnString(this.work, '6') || '-'}/${this.returnString(this.memory, '9') || '-'}`
      : null
  }

  get matrixStringMovement() {
    return `${this.returnString(this.movementMatrix.character, '1')  || '-'}/${this.returnString(this.movementMatrix.health, '4') || '-'}/${this.returnString(this.movementMatrix.luck, '7') || '-'}/${this.returnString(this.movementMatrix.energy, '2') || '-'}/${this.returnString(this.movementMatrix.logic, '5') || '-'}/${this.returnString(this.movementMatrix.dolg, '8') || '-'}/${this.returnString(this.movementMatrix.interest, '3') || '-'}/${this.returnString(this.movementMatrix.work, '6') || '-'}/${this.returnString(this.movementMatrix.memory, '9') || '-'}`
  }

  get codes() {
    return getCodes(this.character, this.energy, this.interest, this.health, this.logic, this.work, this.luck, this.dolg, this.memory, this.temp, this.goal, this.fam, this.habits, this.byt, this.destinyNumb)
  }

  get movementCodes() {
    const moveCodes = getCodes(this.movementMatrix.character, this.movementMatrix.energy, this.movementMatrix.interest, this.movementMatrix.health, this.movementMatrix.logic, this.movementMatrix.work, this.movementMatrix.luck, this.movementMatrix.dolg, this.movementMatrix.memory, this.movementMatrix.temp, this.movementMatrix.goal, this.movementMatrix.fam, this.movementMatrix.habits, this.movementMatrix.byt, this.destinyNumb)
    const codes = {
      codes: moveCodes.codes.filter(c => this.codes.codes.indexOf(c) < 0),
      cheatingcodes: moveCodes.cheatingcodes.filter(c => this.codes.cheatingcodes.indexOf(c) < 0),
      pdcodes: moveCodes.pdcodes.filter(c => this.codes.pdcodes.indexOf(c) < 0),
      occultabilitiescodes: moveCodes.occultabilitiescodes.filter(c => this.codes.occultabilitiescodes.indexOf(c) < 0),
      talentscodes: moveCodes.talentscodes.filter(c => this.codes.talentscodes.indexOf(c) < 0),
    }
    return codes
  }

  get selectedDate() {
    return moment(this.dateField.value, 'DD.MM.YYYY', true).isValid()
      ? moment(this.dateField.value, 'DD.MM.YYYY').toDate()
      : null
  }

  changeForm(formName: 'datepicker' | 'matrix') {
    if (this.activeTab === formName) return
    this.activeTab = formName
    this.resetForms(true)
    formName === 'datepicker' ? this.matrixField.setValue(null) : this.dateField.setValue(null)
  }

  setDate(date: Date) {
    const day = new Date(date).getDate() > 9 ? new Date(date).getDate() : '0' + new Date(date).getDate()
    const month = new Date(date).getMonth() + 1 > 9 ? new Date(date).getMonth() + 1 : '0' + (new Date(date).getMonth() + 1)
    this.dateField.setValue(`${day}.${month}.${new Date(date).getFullYear()}`)
    this.errMsg = ''
  }

  getDate() {
    const date = this.dateField.value?.replaceAll(/\D/g, '')

    if (!date) {
      this.errMsg = ' '
      return
    }

    if (date.length !== 8 || !moment(date, 'DDMMYYYY').isValid()) {
      this.errMsg = '   '
      return
    }

    let additionNumber1 = 0
    let additionNumber2 = 0
    let additionNumber3 = 0
    let additionNumber4 = 0
    for(let i = 0; i < date.length; i++) additionNumber1 += +date[i]
    for(let i = 0; i < String(additionNumber1).length; i++) additionNumber2 += +String(additionNumber1)[i]
    additionNumber3 = additionNumber1 - ( 2 *  (+date[0] == 0 ? +date[1] : +date[0]))
    for(let i = 0; i < String(additionNumber3).length; i++) additionNumber4 += +String(additionNumber3)[i]

    const line = String(date) + additionNumber1 + additionNumber2 + additionNumber3 + additionNumber4

    this.calculateMatrix(line, true)

    if (additionNumber2 > 9 && additionNumber2 !== 11) {
      for(let i = 0; i < String(additionNumber2).length; i++) this.destinyNumb += +String(additionNumber2)[i]
    } else this.destinyNumb = additionNumber2
  }

  calculateMatrix(line, totalyClear = false) {
    if (!line || line && !totalyClear && !this.destinyNumb) {
      this.errMsg = '  '
      return
    }

    this.errMsg = ''

    this.resetForms(totalyClear)
    for (let j = 0; j < line.length; j++) {
      switch(line[j]) {
        case '1':
          this.character += 1
          break;
        case '2':
          this.energy += 1
          break;
        case '3':
          this.interest += 1
          break;
        case '4':
          this.health += 1
          break;
        case '5':
          this.logic += 1
          break;
        case '6':
          this.work += 1
          break;
        case '7':
          this.luck += 1
          break;
        case '8':
          this.dolg += 1
          break;
        case '9':
          this.memory += 1
          break;
      }
    }

    this.temp = this.interest + this.logic + this.luck
    this.goal = this.character + this.health + this.luck
    this.byt = this.health + this.logic + this.work
    this.fam = this.energy + this.logic + this.dolg
    this.habits = this.interest + this.work + this.memory

    this.status = 'show'

    this.movementMatrix = {
      character: this.character,
      energy: this.energy,
      interest: this.interest,
      health: this.health,
      logic: this.logic,
      work: this.work,
      luck: this.luck,
      dolg: this.dolg,
      memory: this.memory,
      temp: this.temp,
      goal: this.goal,
      byt: this.byt,
      fam: this.fam,
      habits: this.habits
    }
  }

  returnString(val: number, text: string) {
    let str = ''
    for (let i = 0; i < val; i++) str += text
    return str
  }

  toggleMovementItem(id: number) {
    const ind = this.selectedMovementItems.indexOf(id)

    // / Reset
    if (
      (id === this.SECTOR_MOVEMENT_IDs.TRANSFORMATION || id === this.SECTOR_MOVEMENT_IDs.DEGRADATION) && ind > -1
      || id !== this.SECTOR_MOVEMENT_IDs.TRANSFORMATION && id !== this.SECTOR_MOVEMENT_IDs.DEGRADATION && (this.selectedMovementItems.includes(this.SECTOR_MOVEMENT_IDs.TRANSFORMATION) || this.selectedMovementItems.includes(this.SECTOR_MOVEMENT_IDs.DEGRADATION))
    ) {
      this.selectedMovementItems = []
      this.movementMatrix.character = this.character
      this.movementMatrix.dolg = this.dolg
      this.movementMatrix.luck = this.luck
      this.movementMatrix.work = this.work
    }

    ind > -1 ? this.selectedMovementItems.splice(ind, 1) : this.selectedMovementItems.push(id)

    // 
    if (id === this.SECTOR_MOVEMENT_IDs.TRANSFORMATION && ind < 0) {
      this.selectedMovementItems = [this.SECTOR_MOVEMENT_IDs.TRANSFORMATION]

      this.movementMatrix.character = this.character % 2 == 0 ? 2 : 1
      this.movementMatrix.dolg = this.dolg + (this.character - (this.character % 2 == 0 ? 2 : 1)) / 2

      this.movementMatrix.work = 0
      this.movementMatrix.luck = this.work + this.luck
    }

    // 
    if (id === this.SECTOR_MOVEMENT_IDs.DEGRADATION && ind < 0) {
      this.selectedMovementItems = [this.SECTOR_MOVEMENT_IDs.DEGRADATION]

      this.movementMatrix.character = this.character + this.dolg * 2
      this.movementMatrix.dolg = 0

      this.movementMatrix.luck = 0
      this.movementMatrix.work = this.work + this.luck
    }

    //   
    if (id === this.SECTOR_MOVEMENT_IDs.CHARACTER && ind < 0) {
      const pairInd = this.selectedMovementItems.indexOf(this.SECTOR_MOVEMENT_IDs.DOLG)
      if (pairInd > -1) this.selectedMovementItems.splice(pairInd, 1)

      this.movementMatrix.character = this.character % 2 == 0 ? 2 : 1
      this.movementMatrix.dolg = this.dolg + (this.character - (this.character % 2 == 0 ? 2 : 1)) / 2
    }
    //   
    if (id === this.SECTOR_MOVEMENT_IDs.DOLG && ind < 0) {
      const pairInd = this.selectedMovementItems.indexOf(this.SECTOR_MOVEMENT_IDs.CHARACTER)
      if (pairInd > -1) this.selectedMovementItems.splice(pairInd, 1)

      this.movementMatrix.character = this.character + this.dolg * 2
      this.movementMatrix.dolg = 0
    }
    // / Reset
    if ((id === this.SECTOR_MOVEMENT_IDs.CHARACTER || id === this.SECTOR_MOVEMENT_IDs.DOLG) && ind > -1) this.resetMovementProperty('character', 'dolg')

    //   
    if (id === this.SECTOR_MOVEMENT_IDs.LOGIC && ind < 0) {
      const pairInd = this.selectedMovementItems.indexOf(this.SECTOR_MOVEMENT_IDs.MEMORY)
      if (pairInd > -1) this.selectedMovementItems.splice(pairInd, 1)

      this.movementMatrix.logic = 0
      this.movementMatrix.memory = this.memory + this.logic * 2
    }
    //   
    if (id === this.SECTOR_MOVEMENT_IDs.MEMORY && ind < 0) {
      const pairInd = this.selectedMovementItems.indexOf(this.SECTOR_MOVEMENT_IDs.LOGIC)
      if (pairInd > -1) this.selectedMovementItems.splice(pairInd, 1)

      this.movementMatrix.memory = this.memory % 2 == 0 ? 0 : 1
      this.movementMatrix.logic = this.logic + (this.memory - (this.memory % 2 == 0 ? 0 : 1)) / 2
    }
    // / Reset
    if ((id === this.SECTOR_MOVEMENT_IDs.LOGIC || id === this.SECTOR_MOVEMENT_IDs.MEMORY) && ind > -1) this.resetMovementProperty('logic', 'memory')

    //   
    if (id === this.SECTOR_MOVEMENT_IDs.LUCK && ind < 0) {
      const pairInd = this.selectedMovementItems.indexOf(this.SECTOR_MOVEMENT_IDs.WORK)
      if (pairInd > -1) this.selectedMovementItems.splice(pairInd, 1)

      this.movementMatrix.luck = 0
      this.movementMatrix.work = this.work + this.luck
    }
    //   
    if (id === this.SECTOR_MOVEMENT_IDs.WORK && ind < 0) {
      const pairInd = this.selectedMovementItems.indexOf(this.SECTOR_MOVEMENT_IDs.LUCK)
      if (pairInd > -1) this.selectedMovementItems.splice(pairInd, 1)

      this.movementMatrix.work = 0
      this.movementMatrix.luck = this.work + this.luck
    }
    // / Reset
    if ((id === this.SECTOR_MOVEMENT_IDs.LUCK || id === this.SECTOR_MOVEMENT_IDs.WORK) && ind > -1) this.resetMovementProperty('work', 'luck')

    this.movementMatrix.temp = this.movementMatrix.interest + this.movementMatrix.logic + this.movementMatrix.luck
    this.movementMatrix.goal = this.movementMatrix.character + this.movementMatrix.health + this.movementMatrix.luck
    this.movementMatrix.byt = this.movementMatrix.health + this.movementMatrix.logic + this.movementMatrix.work
    this.movementMatrix.fam = this.movementMatrix.energy + this.movementMatrix.logic + this.movementMatrix.dolg
    this.movementMatrix.habits = this.movementMatrix.interest + this.movementMatrix.work + this.movementMatrix.memory
  }

  isMovementItemSelected(id: number) {
    return this.selectedMovementItems.indexOf(id) > -1
  }

  selectPage(pageId: 'calculator' | 'catalog' | 'transformation' | 'movement') {
    this.page = pageId
    this.selectedMovementItems = []
    this.movementMatrix = {
      character: this.character,
      energy: this.energy,
      interest: this.interest,
      health: this.health,
      logic: this.logic,
      work: this.work,
      luck: this.luck,
      dolg: this.dolg,
      memory: this.memory,
      temp: this.temp,
      goal: this.goal,
      byt: this.byt,
      fam: this.fam,
      habits: this.habits
    }
  }

  @HostListener('window:scroll', ['$event'])
  onScroll() {
    const d = document.getElementById('matrix-fixed-line')
    const topNav = document.getElementById('top-nav')
    if (window.pageYOffset > 450) {
      d?.classList.add('show')
      topNav?.classList.add('show')
    } else {
      d?.classList.remove('show')
      topNav?.classList.remove('show')
    }
  }

  private resetForms(totalyClear = false) {
    this.character = this.energy = this.interest = this.health = this.logic = this.work = this.luck = this.dolg = this.memory = this.temp = this.goal = this.byt = this.fam = this.habits = 0
    if (totalyClear) this.destinyNumb = null
  }

  private resetMovementProperty(property1: string, property2: string) {
    this.movementMatrix[property1] = this[property1]
    this.movementMatrix[property2] = this[property2]
  }
}
